body {
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom right, #ff94f9, #a8ff64);
}
.App {
  font-family: 'Courier New', Courier, monospace;
  padding: 20px;
  background-color: #1e1e2a;
  color: #a8ff64;
  max-width: 900px; 
  margin: 0 auto;
  border: 3px solid #a8ff64;
  box-shadow: 0 0 15px #a8ff64;
}

.App-header {
  background-color: #2f287f;
  color: #a8ff64;
  padding: 20px 0;
  text-align: center;
  border-bottom: 5px solid #ff94f9;

  h1, h2 {
    margin: 0;
    font-size: 24px; 
  }

  h2 {
    margin-top: 10px;
    color: #ff94f9;
  }
}

.cv-section {
  margin-bottom: 20px;
  border-left: 3px dashed #a8ff64;
  padding-left: 15px;

  h2, h3 {
    font-size: 20px; 
    color: #a8ff64;
    border-bottom: 2px solid #ff94f9;
    padding-bottom: 10px;
  }
}

address, p {
  font-size: 14px;
  color: #ff94f9;
}

ul {
  list-style-type: square;

  li {
    color: #a8ff64;
  }
}

a {
  color: #ff94f9;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
    color: #a8ff64;
  }
}

.images-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1;
}

.draggable-icon img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-bottom: 10px;
}

.date-info {
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background-color: #ff94f9;
  padding: 5px 10px;
  border-radius: 5px;
  display: inline-block;
  margin-bottom: 10px;
}

.image-button {
  position: relative;
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  outline: none;
  display: block;
}

.image-button img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  display: block;
  transition: opacity 0.3s ease;
}

.image-button:hover img {
  opacity: 0.7; /* gives a slightly faded effect when hovered */
}

.hover-text {
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.5); /* semi-transparent black background */
  color: #a8ff64;
  padding: 5px;
  border-radius: 5px;
  pointer-events: none; /* ensure the text doesn't interfere with button clicks */
}

.image-button:hover .hover-text {
  display: block;
}